<template>
  <div>
    <div>
      Русский
      <div v-for="(item, index1) in russian" v-bind:key="index1" >
        <input v-model="russian[index1]" /><p>{{index1}}</p>
      </div>
    </div>
    <div>
      Прото
      <div v-for="(item, index2) in proto" v-bind:key="index2" >
        <input v-model="proto[index2]" /><p>{{index2}}</p>
      </div>
    </div>
    <div>
      <button @click="translateRus">Перевести на Прото</button>
      <button @click="translateProto">Перевести на русский</button>
    </div>
    <div>
      <button @click="change_inputs">Создать окошки</button>
      <input @input="event => inputs = event.target.value">
    </div>
  </div>
  <div>
    <div>Правила</div>
    <div>1. Только 1 слово на Прото 2.Слова на русском пишутся через запятую 3. Заглавные буквы не важны и превратятся в маленькие буквы 4. Если слово на прото уже есть, то русские слова добавятся в конец</div>
    <div>Прото <input v-model="addProto"/></div>
    <div>Русский<input v-model="addRus"/></div>
    <button @click="addWord">Занести в словарь</button>
  </div>
</template>

<script>
import { db } from "@/firebase";
import { doc, getDoc, getDocs, collection, query, where, setDoc } from "firebase/firestore";


export default {
  name: 'Translate',
  data () {
    return {
      inputs: 8,
      russian: ['', '', '', '', '', '', '', ''],
      proto: ['', '', '', '', '', '', '', ''],
      addRus: '',
      addProto: ''
    }
  },
  methods: {
    async translateRus() {
      for(let i=0; i<this.russian.length; i++) {
        const word = this.russian[i].toLowerCase()
        if(word !== undefined && word.length !== 0) {
          try {
            const querySnapshot = collection(db, "proto");
            const q = query(querySnapshot, where("word", "array-contains", word));
            let result = await getDocs(q)
            this.proto[i] = result['docs'][0]['id']
          } catch (e) {
            this.proto[i] = 'неизвестно'
          }
        }
      }
    },
    async translateProto() {
      for(let i=0; i<this.proto.length; i++) {
        const word = this.proto[i].toLowerCase()
        if(word !== undefined && word.length !== 0) {
          try {
            const wordQuery =  doc(db, "proto", word);
            const wordDoc = await getDoc(wordQuery)
            const translated = wordDoc.data()
            this.russian[i] = translated['word'][0]
          } catch (e) {
            this.russian[i] = 'неизвестно'
          }
        }
      }
    },
    change_inputs() {
      let arrayNum = this.inputs
      let arrEmpty = Array(parseInt(arrayNum)).fill('')
      this.russian = arrEmpty
      this.proto = arrEmpty
      this.$forceUpdate
    },
    async addWord() {
      try {
        let ruswords = this.addRus.toLowerCase().replaceAll(' ', '').split(',')

        let data = {
          word: ruswords
        }

        await setDoc(doc(db, "proto", this.addProto), data);

        this.addProto = ''
        this.addRus = ''
      } catch(e) {
        console.log(e)
      }
    }
    // async change_inputs1() {

    // }
  },
  computed: {

  }
}
</script>

